import React from 'react';
import styled from 'styled-components';
import { StaticQuery, graphql } from 'gatsby';
//import { graphql } from 'gatsby'
import { Section, Container } from '@components/global';
//import { get } from "lodash";
import ExternalLink from '@common/ExternalLink';


const SPREADSHEET_QUERY = graphql`
  query eventsQuery {
    allGoogleSheetReleasesRow {
      edges {
        node {
          type
          time
          title
          link
        }
      }
    }
  }
`


const MediaRelease = () => (
  <StaticQuery
    query={SPREADSHEET_QUERY}
    render={data => (
      <Section id="releases" accent="secondary">
        <Container>
          <h3>Press Releases</h3>
          <ReleaseWrapper> 
            {data.allGoogleSheetReleasesRow.edges.map(({node}) => {
              return (
                <Grid>
                <p><small>{node.type}  －  {node.time}</small></p>
                <p><ExternalLink href={node.link}>{node.title}</ExternalLink></p>
                </Grid>
              );
            })}
          </ReleaseWrapper>
        </Container>
      </Section>
    )}
  />
);



const ReleaseWrapper = styled.div`
  padding: 20px 0 20px 0;
  font-weight: 400;
  vertical-align: middle;
  color: ${props => props.theme.color.grayscale.S1000};
  @media (max-width: ${props => props.theme.screen.md}) {
    max-width: 100%;
    padding: 15px 0;
    margin-left: 0%;
  }
`;


const Grid = styled.div`
  display: grid;
  grid-template-columns: 2fr 5fr;
  grid-gap: 20px;
  text-align: left;
  align-items: center;
  justify-items: left;

  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr;
    text-align: left;
    margin-bottom: 10px;
    grid-gap: 5px;

    &:last-child {
      margin-bottom: 24px;
    }
  }

  p{
    margin: 0;
  }
`;



export default MediaRelease;